.row{
    margin-left: 20px;
    color: white;
    margin-top: 1em;
}
.row__poster{
    object-fit: contain;
    max-height: 100px;
    width: 100%;
    transition: transform 450ms;
    margin-right: 10px;
}

.row__posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;

}
.row__posters::-webkit-scrollbar{
    display:none;
}
.row__poster:hover{
    transform: scale(1.08);
}
.row_posterLarge{
    max-height: 250px;
    

}